import Good from '@/components/order/Good.vue'
import Pay from '@/components/common/Pay.vue'
import Dialog from '@/components/common/Dialog.vue'
import {
	getOrderPageMyOrder,
	postOrderConfirm
} from '@/api/userApi.js'
import T from '@/utils/tips.js'
export default {
	name: 'ordlist',
	data() {
		return {
			title: '确认收货吗?',
			hasOrders: false,
			isShow: false,
			orderId: '',
			shopId: '',
			orders: [],
			tabs: [{
					name: '全部',
					status: ''
				},
				{
					name: '待确认',
					status: 0
				},
				{
					name: '待支付',
					status: 2
				},
				{
					name: '待发货',
					status: 3
				},
				{
					name: '待收货',
					status: 4
				}
			],
			pageIndex: 1, // 当前页数
			pageSize: 10, // 每页10条
			status: '', // 订单状态状态 '' 全部 -1 已取消 0 待支付 1 已支付 2 未发货 3 已发货 4已完成 5 已关闭
			allLoaded: false,
			loadText: '上拉加载更多...',
			isPayShow: false,
			nowIndexPrice: 0,
			isWx: true,
			payOrderId: '',
			navIndex: 0,
			platform: 0
		}
	},
	components: {
		Good,
		Pay,
		Dialog
	},
	onReachBottom() {
		this.loadBottom()
	},
	onPullDownRefresh() {
		//监听下拉刷新动作的执行方法，每次手动下拉刷新都会执行一次
		console.log('refresh');
		// 获取订单列表
		this.orders = []
		this.getOrders()
		setTimeout(function() {
			uni.stopPullDownRefresh(); //停止下拉刷新动画
		}, 1000);
	},
	onLoad() {

	},
	onShow() {
		// 设备样式兼容
		this.platform = uni.getStorageSync('platform');
		console.log('platform:', this.platform)
		let orderNavIndex = uni.getStorageSync('orderNavIndex')
		if (orderNavIndex) {
			this.status = orderNavIndex == '1' ? 0 : orderNavIndex
			this.navIndex = orderNavIndex
		}
		// 获取订单列表
		this.orders = []
		this.getOrders()
	},
	methods: {
		doConfirm() {
			let data = {
				orderId: this.orderId,
				shopId: this.shopId
			}
			if (this.orderId && this.shopId) {
				postOrderConfirm(data).then(res => {
					if (res.code === '1000') {
						// 去收货成功页面
						this.isShow = false
						this.goFinshPage()
					} else {
						T.tips(res.message || '确认收货失败')
					}
				}).catch(err => {
					T.tips(err.message || '确认收货失败')
				})
			} else {
				T.tips('确认收货失败')
			}
		},
		doCancel() {
			this.isShow = false
		},
		goDetail(index, orderId, shopId) {
			let item = this.orders[index]
			uni.navigateTo({
				url: '/pages/user/order/detail?orderId=' + item.orderId + '&shopId=' + item.shopId
			})
		},
		goFreight(index) {
			let item = this.orders[index]
			uni.navigateTo({
				url: '/pages/user/order/freight?orderId=' + item.orderId + '&shopId=' + item.shopId
			})
		},
		// 去收货完成页面
		goFinshPage() {
			// orderId, shopId
			uni.navigateTo({
				url: '/pages/user/order/success?orderId=' + this.orderId + '&shopId=' + this.shopId
			})
		},
		// 去支付
		doPay(params) {

			// params 0:微信支付 1:支付宝支付

		},
		// 确认收货
		postOrderConfirm(index) {

			this.isShow = true;
			this.orderId = this.orders[index].orderId;
			this.shopId = this.orders[index].shopId;

		},
		// 显示支付弹窗
		showPay(index) {
			this.nowIndexPrice = this.orders[index].payMoney
			this.payOrderId = this.orders[index].orderId
			this.isPayShow = !this.isPayShow
		},
		// 关闭支付弹窗
		payClose(params) {
			this.isPayShow = !this.isPayShow
		},
		// nav订单状态切换
		changePosi(index) {
			this.navIndex = index
			uni.setStorageSync('orderNavIndex', index)
			// 状态 -1 已取消 0 待支付 1 已支付 2 未发货 3 已发货 4已完成 5 已关闭 6 待审核
			this.status = this.tabs[index].status
			this.orders = []
			this.pageIndex = 1
			this.allLoaded = false
			this.loadText = '上拉加载更多...'
			this.getOrders()
		},
		// 下拉加载更多
		loadBottom() {
			setTimeout(() => {
				if (!this.allLoaded) {
					this.pageIndex++
					this.getOrders()
				}
			}, 500)
		},
		// 获取订单列表
		getOrders() {
			let data = {
				pageIndex: this.pageIndex,
				status: this.status,
				pageSize: this.pageSize
			}
			// 1:货主,2:代办,3:买家
			let roleId = uni.getStorageSync('roleId')
			if (roleId == 20001) {
				data.businessType = 1
			} else if (roleId == 20002) {
				data.businessType = 2
			} else if (roleId == 20003) {
				data.businessType = 2
			}

			getOrderPageMyOrder(data).then((res) => {
				if (res.code === '1000') {
					this.orders = this.orders.concat(res.data.records)
					this.hasOrders = this.orders.length <= 0
					if (this.orders.length >= res.data.total) {
						this.allLoaded = true
						this.loadText = '数据已经加载完毕'
					}
				} else {
					T.tips(res.message || '获取订单列表失败')
					this.hasOrders = this.orders.length <= 0
				}

			}).catch(err => {

				T.tips(err.message || '获取订单列表失败')
			})
		}
	}
}
